
.brand__logo{
	display: flex;
	justify-content: flex-end;
  margin-bottom: 25px;
  img{
    display: block;
    max-width: 100%;
  }
}
.brand__list{
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
  margin: 0 0 0 -20px;
  padding: 0 0 20px;
  list-style: none;
  text-align: left;
}
.brand__list-item{
	width: percentage(1/4);
	padding-left: 20px;
  margin-bottom: 8px;
  @include media('<1210px'){
    width: percentage(1/3);
  }
  @include media('<900px'){
    width: percentage(1/2);
  }
  @include media('<600px'){
    width: percentage(1/1);
  }
}
.brand__list-item-link{
  display: block;
  padding: 4px 8px;
  font-size: 18px;
  line-height: 20px;
  text-decoration: none;
  color: $color__base;
  &:before{
    content: '» ';
  }
}